import React, { useState } from "react";
import Task from './components/Task';
import TaskInput from "./components/TaskInput";

function App() {                                              //  ,     

  const [todo, setTodo] = useState([                          // todo -   , setTodo - ,    todo
    {
      id: 0, title: 'First todo', done: false                 //  useState         todo
    },

    {
      id: 1, title: 'Second todo', done: false
    },

    {
      id: 2, title: 'Third todo', done: false
    }
  ])
  
  return(
    <div className="App">
      <h1 className="top">ToDo list</h1>
      <Task className = 'task' todo = {todo} setTodo = {setTodo} />              {/*   props  todo  setTodo  Task*/}
      <TaskInput todo = {todo} setTodo = {setTodo} />          {/*   props  todo  setTodo  TaskInput*/}
    </div>
  )
}


export default App;
